/** Media Queries **/
.sidebar {
  opacity: 0;
  height: 0;
  overflow: hidden;
  background: linear-gradient(to bottom, #2563EB 0%, #5484ef 100%);
  transition-property: opacity;
  transition-duration: 500ms;
}
.sidebar.mostrar {
  opacity: 1;
  height: 100%;
  padding: 2rem;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: linear-gradient(to bottom, #2563EB 0%, #5484ef 100%);
}
@media (min-width: 768px) {
  .sidebar.mostrar {
    display: none;
  }
}
@media (min-width: 768px) {
  .sidebar {
    display: block;
    height: 100%;
    opacity: 1;
    padding: 2rem;
  }
}
.sidebar .contenedor-sidebar {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.sidebar .contenedor-sidebar h2 {
  color: #FFFFFF;
  margin: 0;
  text-align: center;
}
.sidebar .contenedor-sidebar .cerrar-menu {
  display: flex;
  justify-content: center;
  height: 6rem;
  width: 6rem;
  cursor: pointer;
}
.sidebar .sidebar-nav {
  margin-top: 5rem;
}
.sidebar .sidebar-nav a {
  display: block;
  margin-bottom: 2rem;
  color: #FFFFFF;
  padding: 0.7rem;
  transition: all 300ms ease;
  text-transform: uppercase;
  font-weight: bold;
  font-size: 2.4rem;
  text-align: center;
}
.sidebar .sidebar-nav a:hover {
  background-color: #0e3b9c;
  border-radius: 0.5rem;
}
.sidebar .sidebar-nav a.activo {
  background-color: #0e3b9c;
  border-radius: 0.5rem;
}
@media (min-width: 768px) {
  .sidebar .sidebar-nav a {
    font-size: 1.2rem;
    margin-bottom: 1.5rem;
    text-align: left;
  }
}
.sidebar .cerrar-session-mobile {
  margin-top: 10rem;
  border-radius: 0.5rem;
  background-color: #000;
  text-align: center;
  padding: 2rem;
}
.sidebar .cerrar-session-mobile .cerrar-session {
  color: #FFFFFF;
  font-weight: 700;
  font-size: 2rem;
}
@media (min-width: 768px) {
  .sidebar .cerrar-session-mobile {
    display: none;
  }
}
